@import "../App/config";

.contacts {
    background-color: $grey;
    width: 100%;
    margin-top: -40px;
    position: relative;
    z-index: -1;
    background-image: url('./point.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    .contactsBlock {
        color: white;
        padding-top: 50px;
        padding-bottom: 30px;
        h3 {
            padding-left: 40%; }
        .myContact {
            margin-top: 0;
            display: flex;
            align-items: center;
            padding-left: 45%;
            &:last-child {
                padding-left: 50%; }
            img {
                width: 30px;
                margin: 5px; } } } }


@media screen and (max-width: 880px) {
    .contacts {
        background-size: cover;
        .contactsBlock {
            text-align: center;
            h3 {
                padding-left: 0; }
            .myContact {
                padding-left: 0;
                justify-content: center;
                &:last-child {
                    padding-left: 0; } } } } }
