@import "../../App/config";

.langSwitch {
    display: flex;
    align-items: center;
    position: absolute;
    right: 0;
    p {
        color: $mainColor;
        padding-left: 5px; }
    input[type=checkbox] {
        height: 0;
        width: 0;
        visibility: hidden; }
    label {
        width: 80px;
        height: 40px;
        background: $mainColor;
        display: block;
        border-radius: 160px;
        position: relative;
        &:after {
            content: '';
            position: absolute;
            top: 5px;
            left: 5px;
            width: 30px;
            height: 30px;
            background: white;
            border-radius: 90px;
            transition: 0.3s; } }
    input:checked + label:after {
        left: calc(100% - 5px);
        transform: translateX(-100%); } }

@media screen and (max-width: 680px) {
    .langSwitch {
        right: auto;
        margin-top: 10px; } }
