@import "../App/config";

footer {
    background-color: $grey;
    background-image: url('./point.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    .contactsBlock {
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        padding-top: 20px;
        padding-bottom: 20px;
        .myContact {
            margin: 0 20px;
            display: flex;
            align-items: center;
            img {
                width: 30px;
                margin: 5px; }
            a {
                display: flex;
                align-items: center;
                color: white;
                &:hover {
                    text-decoration: underline; } } } } }
