@import "../../App/config";
$width: 220px;
$height: 300px;

.separateProjectContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 20px;
    .goToProject {
        border-radius: 5px;
        outline: none;
        background-color: $mainColor;
        width: $width;
        padding: 10px 0;
        color: white;
        &:hover {
            box-shadow: 1px 1px 3px black; }
        &:active {
            background-color: $backgroundColor;
            color: $mainColor; } } }

.projectCardContainer {
    margin: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center; }

.projectCard {
    width: $width;
    height: $height;
    position: relative;
    perspective: 1000px;
    &:hover .frontCard {
        transform: rotateY(180deg); }
    &:hover .backCard {
        transform: rotateY(0); }
    .frontCard, .backCard {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
        transition: 1s;
        backface-visibility: hidden;
        border-radius: 10px;
        background-color: white;
        border: 1px solid $mainColor; }
    .frontCard {
        background-color: white;
        border: 1px solid $mainColor;
        align-items: start;
        h3 {
            font-size: 20px;
            margin-top: 5px; }
        img {
            position: absolute;
            width: 100%;
            height: $height - 40px;
            bottom: 0;
            object-fit: cover;
            border-radius: 0 0 10px 10px; } }
    .backCard {
        background-color: $mainColor;
        transform: rotateY(180deg);
        color: white;
        align-items: center; }
    span {
        color:white {}
        padding: 10px; } }
