@import "../../App/config";

.burger-menu {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .menu {
        transition: 0.2s;
        transform: scaleX(0) translateX(0%);
        transform-origin: left center;
        opacity: 0;
        visibility: hidden;
        height: 40px;
        line-height: 40px;
        background-color: $mainColor;
        padding: 0 25px;
        margin-left: -25px;
        border-top-right-radius: 50px;
        border-bottom-right-radius: 50px; }
    a {
        text-decoration: none;
        color: white;
        display: inline-block;
        margin-left: 20px;
        transition: 1s; }
    .menu-btn {
        display: block;
        width: 40px;
        height: 40px;
        background-color: $mainColor;
        border-radius: 50%;
        position: relative;
        z-index: 1;
        margin-left: 0;
        & span,
        & span::before,
        & span::after {
            position: absolute;
            top: 50%;
            margin-top: -1px;
            right: 50%;
            margin-right: -10px;
            width: 20px;
            height: 2px;
            background-color: white; }
        & span::before,
        & span::after {
            content: '';
            display: block;
            transition: 0.2s; }
        & span::before {
            transform: translateY(-5px); }
        & span::after {
            transform: translateY(5px); } }
    .menu-btn_active span:before {
        transform: rotate(-35deg);
        width: 10px;
        transform-origin: right bottom; }
    .menu-btn_active span:after {
        transform: rotate(35deg);
        width: 10px;
        transform-origin: right top; }
    .menu_active {
        transform: scaleX(1) translateX(0%);
        opacity: 1;
        visibility: visible; } }

@media screen and (max-width: 680px) {
    .burger-menu {
        display: none; } }



