@import "../../App/config";
.skillBlock {
    width: 100%;
    span {
        font-style: italic;
        color: $grey;
        font-size: $mainFontSize - 2px; }
    .skillList {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin: 15px 0;
        .first {
            margin: 0 40px 20px 0;
            ul {
                list-style-type: none;
                padding: 0;
                margin: 0 0 10px 0;
                line-height: 26px;
                li:before {
                    content: "</>";
                    color: $mainColor;
                    margin-right: 5px;
                    font-weight: 700; } } }
        img {
            max-width: 40%; } } }
