@import "config";

body {
    margin: 0;
    font-family: $mainFont;
    font-size: $mainFontSize;
    background-color: $backgroundColor; }


.container {
    margin: auto;
    max-width: $contentWidth;
    padding: 20px; }

h1 , h3 {
    @include headerText; }


p {
    @include contentText; }

a {
    @include contentText;
    text-decoration: none;
    color: black; }

button {
    @include contentText; }
