@import "../App/config";

.aboutMe {
    .greyText {
        margin-left: 40%;
        display: flex;
        h6 {
            color: $grey;
            margin: 0;
            margin-right: 25px;
            font-size: 14px; } }
    .aboutContent {
        display: flex;
        align-items: flex-start;
        p {
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0px; } }
        img {
            width: 40%; }
        .article {
            .shortArticle {
                display: -webkit-box;
                -webkit-line-clamp: 12;
                -webkit-box-orient: vertical;
                overflow-y: hidden;
                line-height: $mainFontSize*1.6;
                max-height: $mainFontSize*1.6*15; }
            span {
                color: $grey;
                font-weight: 500;
                font-size: $mainFontSize - 2px; }
            .fullArticle {
                display: block;
                max-height: none; }
            .showFull, .showShort {
                border: none;
                outline: none;
                padding: 10px 0;
                font-size: 14px;
                font-weight: 500;
                color: $grey;
                background-color: $backgroundColor;
                &:hover {
                    color: $mainColor; } }
            .showShort {
                display : none; } } } }

@-moz-document url-prefix() {
    .aboutMe {
        .aboutContent {
            img {
                width: 350px; } }
        .greyText {
            margin-left: 350px; } } }
