@import "../App/config";

.avatarBlock {
    margin-top: 20px;
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 3px 3px 5px black;
    background-image: url('./Avatar.png');
    background-position: 100% 100%;
    background-size: contain;
    background-repeat: no-repeat;
    .menuLine {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        position: relative; }
    .preview {
        @include headerText;
        font-size: $headerFontSize;
        h1:first-of-type {
            padding-left: 40px;
            padding-top: 180px;
            color: $mainColor;
            text-shadow: white 0 0 2px; }
        h1:nth-of-type(2) {
            padding-left: 60px;
            padding-top: 20px;
            color: black; }
        h1:last-of-type {
            padding-left: 80px;
            padding-top: 20px;
            padding-bottom: 100px;
            color: $mainColor;
            text-shadow: white 0 0 1px; } } }

@media screen and (max-width: 680px) {
    .avatarBlock {
        background-image: none;
        .preview {
            h1:first-of-type {
                padding-top: 50px; }
            h1:last-of-type {
                padding-bottom: 20px; } } } }
